import React from "react";
import { motion } from "framer-motion";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line
import { Link } from "react-router-dom";
import { Link as ScrollLink } from "react-scroll";

import useAnimatedNavToggler from "../../helpers/useAnimatedNavToggler.js";

import logo from "../../images/crypto_logo.png";
import { ReactComponent as MenuIcon } from "feather-icons/dist/icons/menu.svg";
import { ReactComponent as CloseIcon } from "feather-icons/dist/icons/x.svg";
import UserProfile from "../../helper/auth/UserProfile.js";

const Header = tw.header`
  flex justify-between items-center
  max-w-screen-xl mx-auto
 
  z-50
`;

export const NavLinks = tw.div`inline-block`;

/* hocus: stands for "on hover or focus"
 * hocus:bg-primary-700 will apply the bg-primary-700 class on hover or focus
 */
export const NavLink = tw.a`
  text-lg my-2 lg:text-sm lg:mx-6 lg:my-0 text-gray-100
  font-semibold tracking-wide transition duration-300
  pb-1 border-b-2 border-transparent hover:border-green-500 hocus:text-green-500
`;

export const PrimaryLink = tw(NavLink)`
  lg:mx-0
  px-8 py-3 rounded bg-green-500 text-gray-100
  hocus:bg-green-700 hocus:text-gray-200 focus:shadow-outline
  border-b-0
`;

export const LogoLink = styled(NavLink)`
  ${tw`flex items-center font-black text-gray-100 border-b-0 text-2xl! ml-0!`};

  img {
    ${tw`w-10 mr-3`}
  }
`;

export const MobileNavLinksContainer = tw.nav`flex flex-1 items-center justify-between `;
export const NavToggle = tw.button`
  lg:hidden z-20 focus:outline-none hocus:text-gray-500 transition duration-300
`;
export const MobileNavLinks = motion(styled.div`
  ${tw`lg:hidden z-10 fixed top-0 inset-x-0 mx-4 my-6 p-8 border text-center rounded-lg bg-gray-900  text-gray-900 `}
  ${NavLinks} {
    ${tw`flex flex-col items-center `}
  }
`);

export const DesktopNavLinks = tw.nav`
  hidden lg:flex flex-1 justify-between items-center
`;

export default ({
  roundedHeaderButton = false,
  logoLink,
  links,
  className,
  collapseBreakpointClass = "lg",
}) => {
  /*
   * This header component accepts an optionals "links" prop that specifies the links to render in the navbar.
   * This links props should be an array of "NavLinks" components which is exported from this file.
   * Each "NavLinks" component can contain any amount of "NavLink" component, also exported from this file.
   * This allows this Header to be multi column.
   * So If you pass only a single item in the array with only one NavLinks component as root, you will get 2 column header.
   * Left part will be LogoLink, and the right part will be the the NavLinks component you
   * supplied.
   * Similarly if you pass 2 items in the links array, then you will get 3 columns, the left will be "LogoLink", the center will be the first "NavLinks" component in the array and the right will be the second "NavLinks" component in the links array.
   * You can also choose to directly modify the links here by not passing any links from the parent component and
   * changing the defaultLinks variable below below.
   * If you manipulate links here, all the styling on the links is already done for you. If you pass links yourself though, you are responsible for styling the links or use the helper styled components that are defined here (NavLink)
   */
  const { showNavLinks, animation, toggleNavbar } = useAnimatedNavToggler();
  const collapseBreakpointCss =
    collapseBreakPointCssMap[collapseBreakpointClass];

  const defaultLogoLink = (
    <Link to="/">
      <LogoLink>
        <img src={logo} alt="logo" />
        CryptoFunnel
      </LogoLink>
    </Link>
  );

  const defaultLinks = [
    <NavLinks key={1}>
      <Link to="/">
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Home
        </span>
      </Link>
      <ScrollLink activeClass="active" to="aboutus" spy={true} smooth={true}>
        <span className="text-white font-semibold mx-5 cursor-pointer">
          About
        </span>
      </ScrollLink>
      {/* <NavLink href="/#">Blog</NavLink> */}
      <ScrollLink activeClass="active" to="pricing" spy={true} smooth={true}>
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Pricing
        </span>
      </ScrollLink>
      <ScrollLink activeClass="active" to="contactus" spy={true} smooth={true}>
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Contact Us
        </span>
      </ScrollLink>
      <Link to="/marketing">
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Marketing
        </span>
      </Link>
      {UserProfile.getRole() == 99 ? (
        <>
          <Link to="/signin">
            <NavLink tw="lg:ml-12!">Login</NavLink>
          </Link>
          <Link to="/signup">
            <PrimaryLink css={roundedHeaderButton && tw`rounded-full`}>
              Sign Up
            </PrimaryLink>
          </Link>
        </>
      ) : (
        <>
          <Link to="/app/dashboard">
            <PrimaryLink css={roundedHeaderButton && tw`rounded-full`}>
              Dashboard
            </PrimaryLink>
          </Link>
        </>
      )}
    </NavLinks>,
  ];

  const MobiledefaultLinks = [
    <NavLinks key={1}>
      <Link to="/">
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Home
        </span>
      </Link>
      <ScrollLink activeClass="active" to="aboutus" spy={true} smooth={true}>
        <div
          onClick={toggleNavbar}
          className="text-white font-semibold hover:cursor-pointer my-4 mx-6 cursor-pointer"
        >
          About
        </div>
      </ScrollLink>
      {/* <NavLink href="/#">Blog</NavLink> */}
      <ScrollLink activeClass="active" to="pricing" spy={true} smooth={true}>
        <div
          onClick={toggleNavbar}
          className="text-white font-semibold hover:cursor-pointer my-4 mx-8 cursor-pointer"
        >
          Pricing
        </div>
      </ScrollLink>
      <ScrollLink activeClass="active" to="contactus" spy={true} smooth={true}>
        <div
          onClick={toggleNavbar}
          className="text-white font-semibold hover:cursor-pointer my-4 mx-8 cursor-pointer"
        >
          Contact Us
        </div>
      </ScrollLink>
      <Link to="/marketing">
        <span className="text-white font-semibold mx-5 cursor-pointer">
          Marketing
        </span>
      </Link>
      <Link to="/signin">
        <NavLink tw="lg:ml-12!">
          <div className="text-white font-semibold hover:cursor-pointer my-4 mx-8 cursor-pointer">
            Login
          </div>
        </NavLink>
      </Link>
      <Link to="/signup">
        <div className="text-white font-semibold hover:cursor-pointer my-4 mt-10 mb-4 curson-pointer">
          <PrimaryLink css={roundedHeaderButton && tw`rounded-full`}>
            Sign Up
          </PrimaryLink>
        </div>
      </Link>
    </NavLinks>,
  ];

  logoLink = logoLink || defaultLogoLink;
  links = defaultLinks;
  let mobilelinks = MobiledefaultLinks;

  return (
    <Header className={className || "header-light"}>
      <DesktopNavLinks css={collapseBreakpointCss.desktopNavLinks}>
        {logoLink}
        {links}
      </DesktopNavLinks>

      <MobileNavLinksContainer
        css={collapseBreakpointCss.mobileNavLinksContainer}
      >
        {logoLink}
        <MobileNavLinks
          initial={{ x: "150%", display: "none" }}
          animate={animation}
          css={collapseBreakpointCss.mobileNavLinks}
        >
          {mobilelinks}
        </MobileNavLinks>
        <NavToggle
          onClick={toggleNavbar}
          className={showNavLinks ? "open" : "closed"}
        >
          {showNavLinks ? (
            <CloseIcon tw="w-6 h-6" />
          ) : (
            <MenuIcon tw="w-6 h-6" />
          )}
        </NavToggle>
      </MobileNavLinksContainer>
    </Header>
  );
};

/* The below code is for generating dynamic break points for navbar.
 * Using this you can specify if you want to switch
 * to the toggleable mobile navbar at "sm", "md" or "lg" or "xl" above using the collapseBreakpointClass prop
 * Its written like this because we are using macros and we can not insert dynamic variables in macros
 */

const collapseBreakPointCssMap = {
  sm: {
    mobileNavLinks: tw`sm:hidden`,
    desktopNavLinks: tw`sm:flex`,
    mobileNavLinksContainer: tw`sm:hidden`,
  },
  md: {
    mobileNavLinks: tw`md:hidden`,
    desktopNavLinks: tw`md:flex`,
    mobileNavLinksContainer: tw`md:hidden`,
  },
  lg: {
    mobileNavLinks: tw`lg:hidden`,
    desktopNavLinks: tw`lg:flex`,
    mobileNavLinksContainer: tw`lg:hidden`,
  },
  xl: {
    mobileNavLinks: tw`lg:hidden`,
    desktopNavLinks: tw`lg:flex`,
    mobileNavLinksContainer: tw`lg:hidden`,
  },
};
